.messages-container {
  display: flex;
  height: calc(100vh - 64px);
  background-color: #f5f5f5;
}

.sidebar {
  width: 320px;
  background: white;
  border-right: 1px solid #e0e0e0;
  display: flex;
  flex-direction: column;
}

.search-header {
  padding: 15px;
  display: flex;
  gap: 10px;
  border-bottom: 1px solid #e0e0e0;
}

.search-bar {
  flex: 1;
}

.search-bar input {
  width: 100%;
  padding: 8px 12px;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  font-size: 14px;
}

.add-friend-btn {
  padding: 8px;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.add-friend-btn:hover {
  background: #0056b3;
}

.tab-buttons {
  display: flex;
  padding: 10px;
  gap: 10px;
  border-bottom: 1px solid #e0e0e0;
}

.tab-buttons button {
  flex: 1;
  padding: 8px;
  border: none;
  border-radius: 8px;
  background: #f0f0f0;
  cursor: pointer;
}

.tab-buttons button.active {
  background: #007bff;
  color: white;
}

.friends-list {
  flex: 1;
  overflow-y: auto;
}

.friend-item {
  display: flex;
  align-items: center;
  padding: 12px 15px;
  border-bottom: 1px solid #f0f0f0;
  cursor: pointer;
  justify-content: space-between;
}

.friend-item > div {
  display: flex;
  align-items: center;
  gap: 12px;
  flex: 1;
}

.friend-item:hover {
  background: #f8f9fa;
}

.friend-item.active {
  background: #e3f2fd;
}

.friend-item img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
}

.friend-info {
  flex: 1;
}

.friend-info h3 {
  margin: 0;
  font-size: 15px;
}

.friend-info p {
  margin: 0;
  font-size: 13px;
  color: #666;
}

.view-profile-btn {
  padding: 6px;
  background: transparent;
  border: none;
  color: #666;
  cursor: pointer;
  border-radius: 50%;
}

.view-profile-btn:hover {
  background: #f0f0f0;
}

.chat-area {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.chat {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.chat-header {
  padding: 15px;
  background: white;
  border-bottom: 1px solid #e0e0e0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.chat-header-info {
  display: flex;
  align-items: center;
  gap: 12px;
}

.chat-header img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
}

.chat-header-actions {
  display: flex;
  gap: 10px;
}

.chat-header-actions button {
  padding: 8px;
  background: transparent;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  color: #666;
}

.chat-header-actions button:hover {
  background: #f0f0f0;
}

.messages {
  flex: 1;
  padding: 20px;
  overflow-y: auto;
  background: #f5f5f5;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.message {
  max-width: 70%;
  padding: 10px 15px;
  border-radius: 12px;
  position: relative;
}

.message.sent {
  align-self: flex-end;
  background: #007bff;
  color: white;
}

.message.received {
  align-self: flex-start;
  background: white;
}

.message-image {
  max-width: 300px;
  border-radius: 8px;
}

.message-footer {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: 4px;
  font-size: 12px;
}

.timestamp {
  color: rgba(255, 255, 255, 0.7);
}

.message.received .timestamp {
  color: #666;
}

.message-actions {
  display: flex;
  gap: 4px;
  opacity: 0;
  transition: opacity 0.2s;
}

.message:hover .message-actions {
  opacity: 1;
}

.message-actions button {
  padding: 4px;
  background: transparent;
  border: none;
  color: inherit;
  cursor: pointer;
  border-radius: 50%;
}

.message-actions button:hover {
  background: rgba(255, 255, 255, 0.1);
}

.read-receipt {
  margin-left: 4px;
}

.message-input {
  padding: 15px;
  background: white;
  border-top: 1px solid #e0e0e0;
  display: flex;
  gap: 10px;
  align-items: center;
}

.message-input input {
  flex: 1;
  padding: 10px;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  font-size: 14px;
}

.message-input button {
  padding: 10px 20px;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.message-input button:hover {
  background: #0056b3;
}

.upload-image-btn {
  padding: 8px;
  background: transparent;
  border: none;
  color: #666;
  cursor: pointer;
  border-radius: 50%;
}

.upload-image-btn:hover {
  background: #f0f0f0;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 12px;
  width: 100%;
  max-width: 400px;
}

.modal-content h3 {
  margin: 0 0 15px 0;
}

.modal-content input {
  width: 100%;
  padding: 8px;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  margin-bottom: 15px;
}

.modal-actions {
  display: flex;
  gap: 10px;
  justify-content: flex-end;
}

.modal-actions button {
  padding: 8px 16px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.modal-actions button:first-child {
  background: #007bff;
  color: white;
}

.modal-actions button:last-child {
  background: #f0f0f0;
}

.no-chat-selected {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f5f5f5;
  color: #666;
}

.unread-badge {
  background-color: #ff3b30;
  color: white;
  border-radius: 50%;
  padding: 2px 6px;
  font-size: 12px;
  margin-left: 8px;
}

.pinned-messages {
  background: #f8f9fa;
  padding: 10px 15px;
  border-bottom: 1px solid #e0e0e0;
}

.pinned-message {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 5px 0;
  color: #666;
  font-size: 14px;
}

.message-actions .pinned {
  color: #007bff;
  fill: #007bff;
}

.messages-container {
  display: flex;
  height: 100vh; /* Fullscreen height */
  background-color: #f5f5f5;
}

.sidebar {
  width: 320px;
  background: white;
  border-right: 1px solid #e0e0e0;
  display: flex;
  flex-direction: column;
  transition: transform 0.3s ease;
}

.sidebar.hidden {
  transform: translateX(-100%); /* Hide sidebar on mobile */
}

.chat-area {
  flex: 1;
  display: flex;
  flex-direction: column;
  width: 100%; /* Ensure it takes full width on smaller screens */
}

@media (max-width: 768px) {
  .messages-container {
    flex-direction: column; /* Stack sidebar and chat area vertically */
  }

  .sidebar {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    z-index: 1000;
    transform: translateX(0); /* Default visible */
  }

  .chat-area {
    flex: none;
  }
}


.back-button {
  padding: 10px;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  margin: 10px;
  display: block;
  text-align: left;
  position: fixed; /* Ensures it stays on top */
  top: 10px;
  left: 10px;
  z-index: 1000;
}

.back-button:hover {
  background: #0056b3;
}
