.forgot-password-container {
    position: relative;
    z-index: 2;
    width: 100%;
    max-width: 440px;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(20px);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 24px;
    padding: 3rem;
    color: white;
    margin: 0 auto;
    margin-top: 50px;
  }


  
  .forgot-password-header {
    text-align: center;
    margin-bottom: 2.5rem;
  }
  
  .forgot-password-header h1 {
    font-size: 2rem;
    font-weight: 700;
    color: white;
  }
  
  .forgot-password-header p {
    color: rgba(255, 255, 255, 0.7);
    font-size: 1rem;
  }
  
  .form-group {
    position: relative;
    margin-bottom: 1.5rem;
  }
  
  .form-input {
    width: 100%;
    padding: 1rem;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 12px;
    font-size: 1rem;
    color: white;
  }
  
  .form-input::placeholder {
    color: rgba(255, 255, 255, 0.4);
  }
  
  .submit-button {
    width: 100%;
    padding: 1rem;
    background: white;
    color: black;
    border: none;
    border-radius: 12px;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
  }
  
  .message-alert {
    color: #4caf50;
    font-size: 0.9rem;
    margin-top: 1rem;
  }
  
  .error-alert {
    color: #ff4477;
    font-size: 0.9rem;
    margin-top: 1rem;
  }
  
  .additional-options {
    margin-top: 1.5rem;
    text-align: center;
    font-size: 0.9rem;
    color: rgba(255, 255, 255, 0.7);
  }
  
  .additional-options a {
    color: white;
    text-decoration: none;
  }
  