/* Reset some basic styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Arial', sans-serif;
  }
  
  /* Main container */
  .chapter-details {
    display: flex;
    flex-direction: column;
    padding: 20px;
    background-color: #f4f4f9;
    height: 100vh;
  }
  
  /* Header */
  .header {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 20px;
    position: relative;
  }
  
  .back-button {
    background-color: transparent;
    border: none;
    font-size: 24px;
    cursor: pointer;
    color: #333;
    transition: color 0.3s;
  }
  
  .back-button:hover {
    color: #007bff;
  }
  
  h1 {
    font-size: 24px;
    margin-left: 10px;
    color: #333;
    font-weight: 600;
  }
  
  /* Sections grid */
  .sections-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
    padding: 20px 0;
  }
  
  /* Section card styling */
  .section-card {
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s, box-shadow 0.3s;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 200px;
    position: relative;
  }
  
  .section-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
  }
  
  .section-card h3 {
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 10px;
    color: #333;
  }
  
  .section-card p {
    font-size: 14px;
    color: #666;
    flex-grow: 1;
    margin-bottom: 10px;
  }
  
  /* Completion badge */
  .completion-badge {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: #28a745;
    color: white;
    padding: 5px 10px;
    border-radius: 50%;
    font-size: 14px;
    font-weight: bold;
  }
  
  /* Smooth animations */
  .section-card,
  .completion-badge {
    animation: fadeInUp 0.5s ease-out;
  }
  
  @keyframes fadeInUp {
    0% {
      opacity: 0;
      transform: translateY(20px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
  