.section-learning {
    padding: 20px;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .header {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
  }
  
  .back-button {
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
    padding: 10px;
    margin-right: 15px;
  }
  
  .lessons-container {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
  }
  
  .lesson-circle {
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    width: 120px;
  }
  
  .circle {
    position: relative;
    width: 80px;
    height: 80px;
  }
  
  .circle svg {
    width: 100%;
    height: 100%;
  }
  
  .circle circle {
    transition: stroke-dasharray 0.3s ease;
  }
  
  .lesson-number {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 24px;
    font-weight: bold;
  }
  
  .lesson-title {
    margin-top: 10px;
    text-align: center;
    font-size: 14px;
  }
  
  .loading-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  .spinner {
    border: 4px solid #f3f3f3;
    border-top: 4px solid #3498db;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    animation: spin 1s linear infinite;
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }