.tab-bar {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: #fff;
    box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.2);
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 10px 0;
    z-index: 1000;
  }
  
  .tab-item {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .icon {
    font-size: 24px;
    color: #555;
    transition: color 0.3s ease;
  }
  
  .icon:hover {
    color: #007bff;
  }
  
  @media (min-width: 768px) {
    .tab-bar {
      display: none;
  }
  }