/* Add these styles to your lio.css file */

.app-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #ffffff;
  transition: all 0.3s ease;
}

.app-container.dark {
  background-color: #1a1a1a;
  color: #ffffff;
}

.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background-color: #f8f9fa;
  border-bottom: 1px solid #dee2e6;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}

.dark .navigation {
  background-color: #2d2d2d;
  border-bottom-color: #404040;
}

.nav-left, .nav-right {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.nav-center {
  flex: 1;
  max-width: 600px;
  margin: 0 2rem;
}

.search-bar {
  display: flex;
  align-items: center;
  background-color: #ffffff;
  border: 1px solid #dee2e6;
  border-radius: 24px;
  padding: 0.5rem 1rem;
}

.dark .search-bar {
  background-color: #404040;
  border-color: #555;
}

.search-bar input {
  border: none;
  background: none;
  outline: none;
  width: 100%;
  margin-left: 0.5rem;
  color: inherit;
}

.sidebar {
  position: fixed;
  left: -300px;
  top: 64px;
  bottom: 0;
  width: 300px;
  background-color: #f8f9fa;
  transition: left 0.3s ease;
  overflow-y: auto;
  z-index: 900;
}

.dark .sidebar {
  background-color: #2d2d2d;
}

.sidebar.open {
  left: 0;
}

.sidebar-content {
  padding: 1rem;
}

.sidebar-section {
  margin-bottom: 2rem;
}

.sidebar-section h2 {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 1.1rem;
  margin-bottom: 1rem;
}

.chat-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin-top: 64px;
  padding: 1rem;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
}

.chat-messages {
  flex: 1;
  overflow-y: auto;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.message {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  max-width: 80%;
  padding: 1rem;
  border-radius: 12px;
}

.message.user {
  margin-left: auto;
  background-color: #007bff;
  color: white;
}

.message.assistant {
  margin-right: auto;
  background-color: #f8f9fa;
}

.dark .message.assistant {
  background-color: #2d2d2d;
}

.message-icons {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.message-content {
  word-break: break-word;
}

.input-area {
  display: flex;
  gap: 0.5rem;
  padding: 1rem;
  background-color: #ffffff;
  border-top: 1px solid #dee2e6;
  margin-top: auto;
}

.dark .input-area {
  background-color: #2d2d2d;
  border-top-color: #404040;
}

.input-area input {
  flex: 1;
  padding: 0.5rem 1rem;
  border: 1px solid #dee2e6;
  border-radius: 24px;
  outline: none;
}

.dark .input-area input {
  background-color: #404040;
  border-color: #555;
  color: white;
}

.input-area button {
  padding: 0.5rem;
  border: none;
  background: none;
  cursor: pointer;
  color: #6c757d;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.input-area button:hover {
  background-color: #f8f9fa;
  color: #007bff;
}

.dark .input-area button:hover {
  background-color: #404040;
}

.mic-button.listening {
  color: #dc3545;
  animation: pulse 1.5s infinite;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

.loading-indicator {
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  padding: 1rem;
}

.loading-indicator div {
  width: 8px;
  height: 8px;
  background-color: #6c757d;
  border-radius: 50%;
  animation: bounce 0.5s infinite alternate;
}

.loading-indicator div:nth-child(2) {
  animation-delay: 0.2s;
}

.loading-indicator div:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes bounce {
  to {
    transform: translateY(-4px);
  }
}

button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Add these styles to your existing CSS */

.voice-mode-toggle {
  padding: 0.5rem;
  border: none;
  background: none;
  cursor: pointer;
  color: #6c757d;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.voice-mode-toggle.active {
  color: #dc3545;
  animation: pulse 1.5s infinite;
}

.dark .voice-mode-toggle.active {
  color: #ff6b6b;
}

.input-area.voice-mode input {
  background-color: #f8f9fa;
  color: #6c757d;
}

.dark .input-area.voice-mode input {
  background-color: #404040;
  color: #adb5bd;
}

/* Update existing loading indicator for voice mode */
.loading-indicator.voice-mode {
  position: fixed;
  bottom: 1rem;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 24px;
  padding: 0.5rem 1rem;
  color: white;
}